/* body */
.body {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 0;
}

/* section 1 - hero-banner*/
.hero-banner {
    margin-top: 85px;
    width: 100%;
    min-height: 560px;
    background-image: url(../assets/img/background_image_hero_banner.svg);
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    background-position: right;

    &__content {
        margin-left: 150px;
        gap: 56px;
        display: flex;
        flex-direction: column;
        width: 100%;

        @include respond-to("large") {
            margin-left: 20px;
        }

        &__content-text {
            display: flex;
            flex-direction: column;
            gap: 8px;

            &__heading {
                font-size: 2.75rem;
                line-height: 4.125rem;
                font-weight: 600;
                color: $black-primary-content;
            }

            &_adv {
                font-size: 1.25rem;
                font-weight: 400;
                color: $grey-content-1;
            }
        }

        &__show-now-btn {
            width: 220px;

            img {
                margin-left: 12px;

            }
        }
    }
}

/* section 2 - strength-points */
.strength-points {
    margin: 50px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 80px;


    &__top-items {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        justify-content: center;
        gap: 20px;

        @include respond-to("huge") {

            grid-template-columns: repeat(2, 1fr);
        }

        &__strength-point-img {
            width: 100%;
            height: auto;
            max-width: 278px;
        }
    }

    &__bottom-items {
        width: 100%;
        display: flex;
        gap: 10px;
        align-items: center;
        justify-content: center;

        @include respond-to("large") {
            flex-direction: column;
        }

        &__content {
            display: flex;
            align-items: center;
            width: 50%;
            border-radius: 16px;
            padding: 20px 0;
            background-repeat: no-repeat;
            overflow: hidden;
            position: relative;

            @include respond-to("colossal") {
                width: 45%;
            }

            @include respond-to("large") {
                width: 82%;
            }

            @include respond-to("medium") {
                width: 100%;
            }

            &__content-text {
                width: 55%;
                margin-left: 16px;
                position: relative;
                z-index: 3;

                @include respond-to("huge") {
                    width: 100%;
                }

                .title {
                    font-size: 2.75rem;
                    line-height: 4.125rem;
                    font-weight: 600;
                    align-items: unset;
                }

                .adv {
                    font-weight: 500;
                    line-height: 1.6875rem;
                    font-size: $normalSize;
                }

                .primary-btn {
                    margin-top: 36px;
                    width: 160px;
                }
            }

            &__mouthwash-bottle-img {
                position: absolute;
                max-width: 250px;
                z-index: 2;
                right: 4%;
            }

            &__hightlight-img {
                top: 0;
                right: 4%;
            }

            &__bundle-img {
                bottom: 0;
                right: 4%;
            }

            &__hightlight-img,
            &__bundle-img {
                position: absolute;
                max-width: 550px;
            }
        }

        &__content:nth-child(1) {
            background-image: url(../assets/img/background_mouthwash.svg);
            color: #fff;

            .primary-btn {
                background-color: $green-secondary-color;

                &:hover {
                    background-color: $green-primary-color;
                    color: #fff;
                }
            }
        }

        &__content:nth-child(2) {
            background-image: url(../assets/img/background_bundle.svg);
        }
    }
}

/* section 3 - trending product */
.trending-products {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 100px 0 50px;

    &__display-list-products {
        width: 100%;
        gap: 4px;
        margin: 60px 0;
        display: flex;
        justify-content: center;


        &__list-products {
            display: flex;
            gap: 20px;
            overflow: hidden;

            .product-card {
                width: 25%;

                @include respond-to("huge") {
                    width: 50%;
                }

                @include respond-to("medium") {
                    width: 100%;
                }
            }
        }
    }
}

/* section 4 - promotional-banner */
.promotional-banner {
    background-image: url(../assets/img/background_promotioanal_banner.svg);
    background-size: cover;
    min-height: 444.38px;
    background-repeat: no-repeat;
    display: flex;
    border-radius: 24px;
    justify-content: flex-end;
    padding: 0;

    @include respond-to("large") {
        background-position: 20% 0;
    }

    @include respond-to("medium") {
        position: relative;
        background-size: unset;
        background-position: unset;
        background-size: 206%;
    }

    &__content {
        display: flex;
        flex-direction: column;
        flex: 1;
        gap: 32.92px;
        max-width: 52%;
        background-color: $blue-background;
        padding: 20px;
        border-radius: 0 24px 24px 0;

        @include respond-to("medium") {
            position: absolute;
            max-width: 100%;
            bottom: -80%;
            border-radius: unset;
            text-align: center;
            align-items: center;
        }

        &__content-text {
            display: flex;
            flex-direction: column;
            gap: 13.17px;
            margin-top: 53px;

            @include respond-to("huge") {
                margin-top: 0;
            }

            &__title {
                font-weight: 700;
                font-size: 2.25rem;
                line-height: 3.375rem;
            }

            &__description {
                color: #000;
                font-weight: 400;
                font-size: 1.125rem;
                line-height: 1.6875rem;
            }
        }

        &__shop-now-btn {
            width: 180px;
        }
    }
}

/* section 5 - testimonials */
.testimonials {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    max-width: 1440px;
    width: 100%;
    background-color: $blue-background;
    gap: 80px;
    padding: 80px 20px;
    margin-top: 100px;

    @include respond-to("medium") {
        margin-top: 500px;
        align-items: unset;
    }

    &__slider {
        display: flex;
        flex-direction: column;
        gap: 60px;

        &__content {
            width: 100%;
            display: flex;
            gap: 16px;
            flex-wrap: nowrap;
            justify-content: flex-start;
            padding: 20px 0;
            overflow: hidden;

            .box-message {
                flex: 0 0 33%;

                @include respond-to("huge") {
                    flex: 0 0 50%;
                }

                @include respond-to("medium") {
                    flex: 0 0 100%;
                    height: unset;
                }
            }
        }
    }
}